import React, { FC } from "react";
import MenuSection from "../MenuSection/MenuSection.lazy";
import "./AboutMe.scss";

interface AboutMeProps {}

const AboutMe: FC<AboutMeProps> = () => {
  return (
    <MenuSection
      icon="address-card"
      id="aboutme-section"
      scrollable
      title="About me"
    >
      {/* {getDays()} */}
      <div className="aboutme" data-testid="AboutMe">
        <span className="aboutme-text">
          I am a web developer. I enjoy building everything from small bussness
          web apps to content rich interative web apps. I love try out new
          Technologies and Frameworks.
        </span>
      </div>
    </MenuSection>
  );
};

export default AboutMe;
